.left__nav__bar {
  position: absolute;
  justify-content: center;
  align-items: center;
  height: 766px;
  width: 80px;
  top: 0;

  background-color: #01134e;

  &__logo {
    margin-top: 18px;
    margin-bottom: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__logo--box {
    width: 35px;
    height: 35px;
   

    background-image: url(../images/logo.svg);
    background-position: center;
    background-repeat: no-repeat;
  }
}
